  .spfxbreadcrumb {
    list-style: none;
    display: inline-block;
  }
  .spfxbreadcrumb:not(:has(li)) {
    display: none;
  }
  
  .spfxbreadcrumb .icon {
    font-size: 14px;
  }
  
  .spfxbreadcrumb li {
    float: left;
    list-style: none !important;
  }
  
  .spfxbreadcrumb li a {
    color: #005437;
    display: block;
    background: #dbdbdb;
    text-decoration: none;
    position: relative;
    height: 24px;
    line-height: 24px;
    text-align: center;
    font-size: 15px;
    margin-right: 14px;
    padding: 0px 3px;
    font-family: 'PTSans';
    margin-top: 5px;
  }
  
  .spfxbreadcrumb li:first-child a {
    padding-left: 5px;
       -moz-border-radius: 4px 0 0 4px;
      -webkit-border-radius: 4px;
      border-radius: 4px 0 0 4px;
  }
  
  .spfxbreadcrumb li:first-child a:before {
    border: none;
  }
  
  .spfxbreadcrumb li:last-child a {
    padding-right: 5px;
    -moz-border-radius: 0 4px 4px 0;
    -webkit-border-radius: 0 4px 4px 0;
    border-radius: 0 4px 4px 0;
    color:#000000c9;
  }
  
  .spfxbreadcrumb li:last-child a:after {
    border: none;
  }
  
  .spfxbreadcrumb li a:before,
  .spfxbreadcrumb li a:after {
    content: "";
    position: absolute;
    top: 0;
    border: 0 solid #dbdbdb;
    border-width: 12px 6px;
    width: 0;
    height: 0;
  }
  
