  summary {
    outline: none;
    display: block;
    background: var(--primary-color);
    color: var(--light);
    position: relative;
    cursor: pointer;
  }
  
  .AccordionContent {
    border: 1px solid #ddd;
    border-top: none;
    background: var(--white);
  }
  
  summary::before {
    content: "";
    position: absolute;
    display: block;
    width: 12px;
    height: 12px;
    transition: transform .3s;
    transform: translateY(-100%) rotate(45deg);
    border-right: 1.5px solid #ffffff;
    border-bottom: 1.5px solid #ffffff;
    top: 24px;
    right: 20px;
  }
  
  summary.primary-color::before {
    border-right: 1.5px solid var(--primary-color);
    border-top: 1.5px solid var(--primary-color);
  }
  
  details[open]>summary:before {
    transform: translateY(-45%) rotate(224deg) !important;
    transition: 0.45s transform ease !important;
  }
  
  summary.bg-primary-color:hover strong,
  details[open] summary.bg-primary-color strong,
  summary:hover::before,
  details[open] summary.bg-primary-color::before {
    color: var(--white);
  }
  
  /* # The Sliding Summary # */
  details {
    overflow: hidden;
    margin-bottom: 15px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
  }
  
  details summary {
    /* position: relative; */
    z-index: 10;
    font-family: var(--body-font);
    display: inline-block;
    width: 100%;
    font-size: 1.1875rem;
    padding: 8px 20px;
    background: var(--primary-color);
  }
  
  details[open]>summary {
    background: var(--hover-color);
  }
  
  details[open] .expand-AccordionContent.clearfix {
    padding: 16px 20px;
  }
  
  @keyframes details-show {
    from {
      margin-bottom: -80%;
      opacity: 0;
      transform: translateY(-100%);
    }
  }
  
  details>*:not(summary) {
    /*animation: details-show 100ms ease-in-out;*/
    /* position: relative;
    z-index: 1; */
    transition: all 0.2s ease-in-out;
    color: transparent;
    overflow: hidden;
  }
  
  details[open]>*:not(summary) {
    color: inherit;
  }
  
